import React from 'react';
import PropTypes from 'prop-types';
import './stylesheet.css'
import { Link } from 'react-router-dom';



export default function Blog(props) {
  return (
    <>
      <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`}>
        <div className="container-fluid">
          <Link className="navbar-brand" to="/">{props.navTitle}</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link" aria-current="page" to="/">Home</Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" aria-current="page" to="/about">{props.about}</Link>
              </li>




            </ul>
            <div className="form-check form-switch">
              <input className="form-check-input" type="checkbox" id="flexSwitchCheckChecked" onClick={props.toggleMode} />
                <label className={`form-check-label text-${props.textColor}`} htmlFor="flexSwitchCheckChecked" >Enable dark mode</label>
            </div>
          </div>
        </div>
      </nav>

    </>
  );
}

Blog.propTypes = {
  navTitle: PropTypes.string,
  about: PropTypes.string
}
Blog.defaultProps = {
  navTitle: "Default",
  about: "Default About"
}